/*-------------------------------------------------------*/
/* Helper Classes
/*-------------------------------------------------------*/


/* Bottom Gradient
-------------------------------------------------------*/
.bottom-gradient {
  position: absolute;
  width: 100%;
  height: 70%;
  bottom: 0;
  z-index: 1;
  @include linear-gradient-from-bottom(rgba(#000, .7), transparent);
}


/* Section Dividers
-------------------------------------------------------*/
.top-divider {
  border-top: 1px solid $border-color;
}

.bottom-divider {
  border-bottom: 1px solid $border-color;
}


/* Image Floats
-------------------------------------------------------*/
.alignright {
  float: right;
  margin: 10px 0 10px 34px; }

.alignleft {
  float: left;
  margin: 10px 34px 10px 0; }